.bah-aid-top {
	height: 260px; 
	background-color: white;
}

.bah-aid-bottom {
	height: 260px; 
	background-color: #f7f7f7;
}

.bah-image-1 {
	width: 250px; 
	top: 15px;
}

.application-process-radios {
	height: 225px; 
	background-color: #f7f7f7;
	color: #4ed08d;
	& p {
		color: black;
	}
}

.application-process-radios:hover {
	height: 225px; 
	background-color: #4ed08d; 
	color: white;
	& p {
		color: white;
	}
}

.application-process-radios.active {
	height: 225px; 
	background-color: #4ed08d; 
	color: white;
	& p {
		color: white;
	}
}


@media (max-width: 991px) {
	.bah-image-1 {
		width: 140px;
    	top: 125px;
	}

	.px-8per {
		padding-left: 0;
		padding-right: 0;
	}

	.bah-images-responsive {
		display: none;
	}
}

@media (max-width: 767px) {
	.bah-aid-bottom {
		height: 330px;
	}
}

@media (max-width: 489px) {
	.bah-aid-top {
		height: 310px;
	}

	.bah-aid-bottom {
		height: 370px;
	}

	.bah-image-1 {
		top: 175px;
	}
}

