.size-btn-01 {
	width: 128px;
	height: 37px;
}

.m-btn_close-tr {
	position: absolute !important;
	top: -10px;
    right: -10px;
    transition: transform .2s;
}

.m-btn_close-tr:hover {
	transform: scale(1.5);
}

.create-new-tour {
	&__checkbox {
		bottom: 0;
		left: 33px;
	}

	&__textarea {
		height: 160px;
		width: 100%;
	}
}


/*Begin Create new tour*/
.images-size {
	&__w1 {
		width: 42px;
		height: 42px;
	}
}

.m-btn-upload {
	height: 35px; 
	width: 160px; 
	border: 1px solid #DDDDDD; 
	border-radius: 20px; 
	background: linear-gradient(#F6F6F6, #FFFFFF);
}


.table-tbody_pricing tr th {
	min-width: 355px;
	vertical-align: middle;
	border-top: 0px;
}

.table-tbody_pricing tr td {
	vertical-align: middle;
}

.cnt-cover__image {
	width: 257px; 
	height: 129px;
}

.table-striped > tbody > tr:nth-child(2n+1) > td, 
.table-striped > tbody > tr:nth-child(2n+1) > th {
   background-color: white;
   border-top:0;
}

.table-striped > tbody > tr:nth-child(2n) > td, 
.table-striped > tbody > tr:nth-child(2n) > th {
   background-color: rgba(0,0,0,.05);
   border-top:0;
}

.table-striped > thead > tr {
   background-color: rgba(0,0,0,.05);
}
/*End Create new tour*/

@media (max-width: 1199px){
	.m-col-sm-mt-0 {
		margin-top: 0 !important;
	}
}

@media (max-width: 660px){
	.table-tbody_pricing tr th {
		min-width: 150px;
	}
}