.hiw_frame-step1 {
	background-color: white; 
	height: 325px; 
	z-index: 2;
}

.hiw_frame-step2 {
	height: 325px; 
	z-index: 2;
}

.hiw-images1 {
	width: 90%; 
	height: 265px;
}

.hiw {
	&_style-background1 {
		position: absolute;
		width: 100%;
		background-color: white;
		height: 230px;
		top: 72px;
	}
	&_style-background2 {
		position: absolute;
		width: 100%;
		background-color: white;
		height: 210px;
		bottom: 0;
	}
}

@media (max-width: 688px) {
	.hiw_style-background1 {
		top: 124px;
	}
}

@media (max-width: 767px) {
	.hiw_frame-step1,
	.hiw_frame-step2 {
		height: 540px
	}
}