.header {
    background: #4ed08d;
    font-size: 15px;
    &-head{
        .notification{
            a{
                font-weight: 400;
                font-size: 16px;
                color: #000000;;
            }
            a:hover{
                text-shadow: none;
            }
            p{
                padding: 10px 10px 10px 20px;
                border-bottom: 1px solid #ededed;
            }
            p:hover{
                background-color: #D9FFEB;
            }
            &__icon{
                background-color: #D0021B;
                border-radius: 20px;
                width: 17px;
                height: 17px;
                box-shadow: 0px 0px 5px rgb(162, 4, 22);
                top: -5px;
                left: 13px;
                span{
                    color: #ffffff;
                    padding-left: 4px;
                    font-size: 13px;
                    font-weight: 600;
                    padding-top: -10px;
                    margin-top: -10px;
                    top: 8px;
                }
            }
            .menu{
                max-width: 300px;
            }
        }
        .userbar{
            a{
                font-weight: 400;
                font-size: 16px;
                color: #191919;
                border-bottom: 1px solid #ededed;
                padding: 8px 30px;
            }
            a:active{
                background-color: #ededed;
            }
        }
        a{
            color: white;
            font-weight: 600;
        }
        a:hover{
            text-decoration: none;
            text-shadow: 1px 1px 20px;
        }
    }
    &__list {
        margin: 15px 0px;
        padding: 0 0 0 16px;
    }
    &__social{
        background-color: #444444;
        opacity: 0.4;
        padding: 0px 5px;
        margin-left: 5px;
    }
    &__social:hover{
        background-color: white;
    }
    &-menu{
        background-color: white;
        font-weight: 600;
        a{
            font-size: 15px;
            color: #444444;
            margin: 23px 0px 17px 35px;
            padding-bottom: 5px;
            text-decoration: none;
            border-bottom: 2px solid white;
        }
        a:hover{
            border-bottom: 2px solid#4ed08d;
        }
    }
    &-menu__logo{
        position: absolute;
        top: 0px;
        &-scroll{
            display: none;
        }
    }
    &-menu__logo:hover{
        transform: scale(1, 1) rotate(2deg);
    }
    &-mobile-menu{
        font-weight: 600;
        visibility: hidden;
        position: absolute;
        z-index: 99;
        width: 100%;
        padding: 10px 0px ;
        background: white;
        transition: all .5s ease;
        transform: translateX(-100%);
        &.active {
            visibility: visible;
            opacity: 1;
            transform: translateX(0);
            position: absolute;
        }
        a{
            font-size: 15px;
            color: #444444;
        }
        a:hover{
            color:  #4ed08d;
        }
    }
    &-mobile {
        height: 60px;
        &__logo {
            width: 100px;
            position: relative;
        }
    }
}


.show-hang {
    position: fixed;
    top: 0;
    z-index: 99;
    width: 100%;
    .py-3{
        padding-top: 3px !important;
        padding-bottom: 0px !important;
    }
    .header{
        &-mobile{
            .bg-white{
                background-color:#0c8d4b !important;
            }
        }
        &-menu{
        background-color:#0c8d4b;
            a{
                color: white;
                padding-bottom: 2px;
                margin: 0px 0px 0px 35px;
                border-bottom: 1.5px solid #0c8d4b;
            }
            a:hover{
                border-bottom: 1.5px solid white;
            }
        }
        &-mobile__logo{
            display: none;
        }
        &-menu__logo{
            display: none;
        }
        &-menu__logo-scroll {
            height: 30px;
            display: block;
        }
        &-menu__logo-scroll:hover{
            transform: scale(1, 1) rotate(2deg);
            border-style: none;
        }
    }
}

@media (max-width: 1200px) {
    .header-menu__logo{
        top: 40px;
        width: 200px;
    }
}