/* The container */
.label-checkbox {
    display: block;
    position: relative;
    cursor: pointer;
    height: 100%;
    margin: 0;
}

/* Hide the browser's default checkbox */
.label-checkbox input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
}

/* Create a custom checkbox */
.checkmark-unchecked {
  color: #bbbdc1;
}

.checkmark-unchecked,
.checkmark-checked {
    position: absolute;
    bottom: -20px;
    left: 40px;
    height: 25px;
    width: 25px;
}

/* On mouse-over, add a grey background color */
.label-checkbox:hover input ~ .checkmark {
    
}

/* When the checkbox is checked, add a blue background */
.label-checkbox input:checked ~ .checkmark {
    
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark-checked {
    color: #4FD08D;
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */
.label-checkbox input:checked ~ .checkmark-checked {
    display: block;
}

