.banner {
    height: 515px;
    &__form {
        border-radius: 3px;
        background: rgba(0, 0, 0, 0.6);
        width: 835px;
        height: 115px;
        padding: 27px 27px;
        position: relative;
    }
    &__input {
        width: 100%;
        height: 100%;
        font-size: 20px;
        padding: 0 66px 0 22px;
        &:focus {
            outline: none;
        }
    }
    &__icon {
        position: absolute;
        top: 38%;
        right: 5%;
        &:hover {
            transform: scale(1.3, 1.3) rotate(0);
        }
    }
}
.testmonial{
    padding-bottom: 25px;
    background: #f7f7f7;
    .carousel-item{
        padding: 0px 80px;
    }
    .carousel-control-prev{
        img{
            margin-left: -65%;
        }
    }
    .carousel-control-next{
        img{
            margin-right: -65%;
        }
    }
}

.cook {
    padding: 75px 0 65px;

    &__box {
        position: relative;
        overflow: hidden;
        img {
            transition: all 0.6s ease-in-out;
        }
        &:hover img {
            transform: scale(1.3, 1.3) rotate(1deg);
        }
    }
}

.map {
    background: #4ed08d;
    &__title {
        padding: 63px 0 65px;
    }
    &__process {
        max-width: 910px;
        margin: auto;
    }
    &__box {
        flex-direction: column;
        border-radius: 50%;
        transition: all 0.3s ease;
        width: 250px;
        height: 250px;
        background: #d0fee6;
        img {
            transition: all 0.3s ease;
            margin: 0 0 15px 0;
        }
        h4 {
            transition: all 0.3s ease;
            width: 100%;
            margin: 15px 0 0 0;
            padding: 0 50px;
            text-align: center;
            font-size: 20px;
            font-weight: 500;
            color: #446252;
        }
        &:hover {
            background: #ffffff;
            text-decoration: none;
            img {
                transform: scale(1.2, 1.2) rotate(0);
            }
            h4 {
                color: #403f3f;
            }
        }
    }
    &__world {
        position: relative;
        padding: 30px 0 7px 0;
    }
    &__vn {
        position: absolute;
        top: 56.5%;
        right: 12.15%;
        list-style: none;
        li:first-child {
            animation: gyrate3D 2s infinite linear;
            transform-style: preserve-3d;
            @keyframes gyrate3D {
                from {
                    transform: rotateY(0deg);
                }
                to {
                    transform: rotateY(360deg);
                }
            }
            display: inline-block;
            img {
                transition: all 0.3s ease;
            }
        }
        li:last-child {
            display: inline-block;
            width: 118px;
            height: 50px;
            img {
                transition: all 0.3s ease;
                margin: 0 0 13px 3px;
                width: 0;
            }
        }
        &:hover {
            li:last-child {
                img {
                    width: 100%;
                }
            }
        }
    }
}

.host {
    &__title {
        margin: 100px 0 69px;
    }
    &__box {
        margin: 0 0 30px 0;
        @extend %box;
    }
}

.destination {
    &__title {
        margin: 62px 0 67px;
    }
    &__box {
        margin: 0 0 30px 0;
        @extend %box;
        .view-effect {
            border: solid 3px #b9b9b9;
            .content {
                padding: 0 6.5%;
            }
            &:hover {
                box-shadow: 1px 1px 4px #ffffff;
                border: solid 3px #ffffff;
            }
        }
    }
}

.three-part{
    h3{
        font-size: 25px;
        font-weight: 600;
        a{
            color: #1b1b1b;
        }
        a:hover{
            color: #4ed08d;
            text-decoration: none;
        }
    }
    p{
        font-size: 18px;
        font-weight: 300;
        line-height: 23px;
    }
}


%box {
    .view-effect {
        overflow: hidden;
        .content {
            transition: all 0.3s ease;
            position: relative;
            top: -217px;
            padding: 0 7.5%;
            h2 {
                margin: 0;
                padding: 14% 0 0;
                font-size: 20px;
                font-weight: 700;
                color: #ffffff;
            }
            p {
                margin: 0;
                padding: 4% 0;
                font-size: 17px;
                font-weight: 300;
                line-height: 18px;
                color: #ffffff;
                &:last-child {
                    padding-bottom: 11%;
                }
            }
            a {
                transition: all 0.3s ease;
                border-radius: 20px;
                box-sizing: border-box;
                color: #ffffff;
                border: solid 2px #13b361;
                background: #13b361;
                &:hover {
                    color: #13b361;
                    background: #ffffff;
                }
                display: block;
                width: 100%;
                padding: 5% 0;
                font-size: 17px;
                font-weight: 600;
                text-align: center;
            }
        }
        &:hover {
            .content {
                background: rgba(0, 0, 0, 0.6);
                top: 0;
                background-clip: border-box;
                @keyframes box-down {
                    0% {
                        top: -217px;
                    }
                    16.65% {
                        top: -67px;
                    }
                    33.3% {
                        top: 0;
                    }
                    49.95% {
                        top: -27px;
                    }
                    66.6% {
                        top: 0px;
                    }
                    83.25% {
                        top: -7px;
                    }
                    100% {
                        top: 0;
                    }
                }
            }
        }
    }
}
@media only screen and (max-width: 1199px) and (min-width: 992px) {
    .host, .destination {
        &__title {
            a {
                font-size: 27px;
            }
        }
        &__box {
            .view-effect {
                .content {
                    h2 {
                        font-size: 17px;
                    }
                    p {
                        font-size: 13px;
                    }
                    a {
                        font-size: 13px;
                    }
                }
            }
        }
    }
}
@media (max-width: 991px) {
    .map {
        &__box {
            width: 200px;
            height: 200px;
            h4 {
                width: 100%;
                margin: 0;
                padding: 0 30px;
                font-size: 18px;
            }
        }
        &__vn {
            top: 54%;
            right: 5.2%;
        }
    }
}

