.m-prev-about {
	justify-content: flex-start;
	left: -31px;
	opacity: 1;
}

.m-next-about {
	justify-content: flex-end;
	right: -31px;
	opacity: 1;
}

.about {
	&__wwo-frame1 {
		background-color: #ebebeb; 
		border-width: 2px !important;
	}

	&__slide-style {
		padding: 130px 19%;
	}

	&__hws-frame1 {
		height: 460px;
	}
	&__hws-frame2 {
		padding: 0 23%;
	}
}

.about-map__vn {
        position: absolute;
        top: 54.9%;
    	right: 3%;
        list-style: none;
        li:first-child {
            animation: gyrate3D 2s infinite linear;
            transform-style: preserve-3d;
            @keyframes gyrate3D {
                from {
                    transform: rotateY(0deg);
                }
                to {
                    transform: rotateY(360deg);
                }
            }
            display: inline-block;
            img {
                transition: all 0.3s ease;
            }
        }
        li:last-child {
            display: inline-block;
            width: 118px;
            height: 50px;
            img {
                transition: all 0.3s ease;
                margin: 0 0 13px 3px;
                width: 0;
            }
        }
        &:hover {
            li:last-child {
                img {
                    width: 80%;
                }
            }
        }
}

@media (max-width: 425px) {
	.about {
		&__hws-frame1 {
			height: 570px;
		}
	}
}

@media (max-width: 767px) {
	.about {
		&__slide-style {
			padding: 50px 2%;
		}
		&__hws-frame2 {
			padding: 30px 10%;
		}
	}
}

@media (max-width: 991px) {
	.mx-8per {
		margin-left: 0;
		margin-right: 0;
	}
}