.m-btn {
    padding: 10px 20px;
    border-radius: 3px;
    font-size: 19px;
    &:hover {
        text-decoration: none;
    }
}
.m-btn1 {
    @extend .m-btn;
    padding: 10px 30px;
    font-size: 15px;
    font-weight: 600;
}
.m-btn2 {
    @extend .m-btn;
    font-size: 15px;
}
.m-btn3 {
    @extend .m-btn;
    padding: 5px 15px;
    font-size: 16px;
}
.m-btn-green {
    border: none;
    color: #FFFFFF;
    background: #4ED08D;
    &:hover {
        color: #FFFFFF;
        background:#1BBA68 ;
    }
}

.m-btn-grey-01 {
    border: none;
    color: #FFFFFF;
    background: #A4A4A4;
    &:hover {
        color: #FFFFFF;
        background: #C3C3C3 ;
    }
}

.m-btn-green-white {
    border: 1px solid #4ED08D;
    background-color: #FFFFFF;
    &:hover {
        color: #FFFFFF;
        background:#4ED08D;
    }
}
.m-btn-blue {
    border: none;
    color: #FFFFFF;
    background: #4F8AD0 ;
    &:hover {
        color: #FFFFFF;
        background:#3775A8;
    }
}

.m-btn-white {
    border: 1px solid rgba(#E2E2E2, 0.5);
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);
    color: #FFFFFF;
    background: transparent;
    &:hover {
        color: #FFFFFF;
        border-color: #fff;
    }
}
.m-btn-white1{
    border: 0.4px solid #a3a3a3;
    background-color: #ffffff;
    &:hover {
        color: #FFFFFF;
        background-color: #dddddd;
    }
}
.px-8per {
    padding-left: 8%; 
    padding-right: 8%
}

.mx-8per {
    margin-left: 8%; 
    margin-right: 8%
}

.m-width {
    &__w-315 {
        width: 315px;
    }
    &__w-280 {
        width: 280px;
    }
    &__w-130 {
        width: 130px;
    }
}

.m-line-light-greyish {
    display: block;
    width: 100%;
    border-top: 1px solid #D8D8D8;
}

.bb-1{
    border-bottom: 1px solid #ededed;
}
.br-1{
    border-right: 1px solid #ededed;
}
.bt-1{
    border-top: 1px solid #ededed;
}