.host-profile-step{
    &__nav{
        font-size: 18px;
        .nav{
            background-color: #f5f5f5;
            .active{
                background-color: #4fd08d;
                color: #ffffff;
                span{
                    color: #ffffff;
                }   
            }
        }
    }
    &__content{
        background-color: #f5f5f5;
    }
    &__title{
    font-size: 20px;
    font-weight: 700;
    }
    &__text1{
        font-size: 13px;
        color: #aeb9b4;
        line-height: 24px;
    }
    &__text2{
        opacity: 0.67;
        font-size: 10px;
        span{
            color: #4FD08D;
        }
        span:hover{
            color: rgb(46, 125, 84);
            text-decoration: none;
        }
    }
    &__text3{
        font-size: 15px;
        line-height: 26px;
        span{
            font-weight: 600;
        }
    }
    &__text4{
        font-size: 18px;
    }
    &__text5{
        font-size: 15px;
        color: #a7a6a6;
    }
    &__text6{
        font-size: 14px;
        font-weight: 500;
    }
    &__wrap{
        background-color: #EDFFF6;
        border: 1px solid #9CEEC3;
        border-radius: 10px;
        padding: 20px;
        img{
            margin-bottom: 5px;
        }
    }
    &__part button{
        height: 138px;
        background-color: #ffffff;
        border:none;
    }
    &__part button:hover{
        background-color: #4fd08d;
    }
    &__part button:active{
        background-color: #4fd08d;
    }
    &__part button:focus{
        background-color: #4fd08d;
    }
    .carousel-control-prev, .carousel-control-next {
         visibility: hidden;
    }
    .carousel:hover{
        .carousel-control-prev, .carousel-control-next {
            visibility: visible;
       }
    }

    &-radios {
    height: 140px; 
    background-color: white;
        & p {
            color: black;
        }
    }

    &-radios:hover {
    height: 140px; 
    background-color: #4ed08d; 
    color: white;
        & p {
            color: white;
        }
    }

    &-radios.active {
    height: 140px; 
    background-color: #4ed08d; 
    color: white;
        & p {
            color: white;
        }
    }
}