.hl-item{
    &__bg-effect{
        background: rgba(0, 0, 0, 0.6);
    }
    &__bg-effect-w{
        background: rgba(255, 255, 255, 0.8);
    }
    &__bg-effect250{
        background: rgba(0, 0, 0, 0.6);
        height: 250px;
    }
    &__bg-date {
        padding-right: 30px !important;
        background-image: url(../images/ic-date-range.svg) !important;
        background-repeat: no-repeat !important;
    }
    &__w425 {
        width: 425px;
        background-position: 390px center !important;
    }
    /* Begin Effect Images Become a host */
    &__img-effect {
        width: 100%;
        height: 100%;
        background-color: black;
        opacity: 0.4;
    }
    /* End Effect Images Become a host */

    &__title1{
        font-size: 38px;
        font-weight: 600;
        color: white;
        text-align: center;
    }
    &__title2{
        font-size: 30px;
    }
    &__title3{
        font-size: 20px;
    }
    &__title4{
        font-size: 20px;
        font-style: italic;
        color: #b5b5b5;
    }
    &__title5{
        font-size: 34px;
        font-weight: 500;
        color: #4ed08d;
    }
    &__title5:hover{
        text-decoration: none;
        color: rgb(51, 144, 96);
    }
    &__title6{
        font-size: 18px;
        font-weight: 700;
        color: #474747;
    }
    &__title6:hover{
        text-decoration: none;
        color: #4ed08d;
    }
    &__title7{
        @extend .hl-item__title5;
        color: #ffffff;
        font-weight: 700;
    }
    &__title8{
        @extend .hl-item__title7;
        color:  #13b361;
    }

    &__label1{
        font-size: 35px;
        font-weight: 600;
    }
    &__label2{
        font-size: 35px;
        font-weight: 700;
        color: #4ED08D;
    }
    &__text1{
        font-size: 16px;
    }
    &__text2{
        font-size: 16px;
        font-weight: 600;
        color: white;
    }
    &__text3{
        font-size: 18px;
    }
    &__text4{
        font-size: 20px;
        font-weight: 300;
    }
    &__text5{
        font-size: 15px;
        color: #c5c5c5;
    }
    &__text6{
        font-size: 14px;
        font-weight: 500;
    }
    &__text7{
        font-size: 16px;
        font-weight: 300;
        line-height: 21px;
    }
    &__text8{
        font-size: 18px;
        font-weight: 600;
        color: #4ed08d;
    }
    &__text9{
        font-size: 20px;
        font-weight: 600;
        color: #000000;
    }

    &__link1{
        font-size: 16px;
        color: #4ED08D;
    }
    &__link1:hover{
        text-decoration: none;
        color: rgb(51, 144, 96);
    }
    &__link2{
        @extend .hl-item__link1;
        font-size: 17px;
        font-weight: 300;
    }
    &__bb{
        border-bottom: 1px solid #c6c5c5;
    }

    /************Nguyen Font************/
    /* Begin title How it works */
    &__title5-48 {
        font-size: 48px;
        font-weight: 600;
        color: #4ED08D;
    }
    &__title6-32 {
        font-size: 32px;
        font-weight: 400;
    }
    /* End title How it works */

    /* Begin title About */
    &__title7-24 {
        font-size: 24px;
        font-weight: 400;
    }
    /* End title About */

    /* Begin title Founders */
    &__title8-36 {
        font-size: 36px;
        font-weight: 400;
    }
    &__title9-25 {
        font-size: 25px;
        font-weight: 600;
    }
    /* End title Founders */

    /* Begin title Host Dashboard */
    &__title10-18 {
        font-size: 18px;
        font-weight: 600;
    }
    &__title11-27 {
        font-size: 27px;
        font-weight: 500;
    }
    &__title12-17 {
        font-size: 17px;
        font-weight: 600;
    }
    &__title13-19 {
        font-size: 17px;
        font-weight: 600;
    }
    /* End title Host Dashboard */

    /* Begin title Tourist Dashboard */
    &__title14-26 {
        font-size: 26px;
        font-weight: 600;
        color: #FFFFFF;
    }
    /* End title Tourist Dashboard */

    /* Begin label Host Dashboard */
    &__label2-15{
        font-size: 15px;
        font-weight: 600;
    }
    /* End label Host Dashboard */

    /* Begin text Become a host */
    &__text5-22{
        font-size: 22px;
        font-weight: 400;
    }
    &__text6-22{
        font-size: 22px;
        font-weight: 600;
    }
    /* End text Become a host */

    /* Begin text Founders */
    &__text7-18 {
        text-transform: uppercase;
        font-size: 18px;
        font-weight: 1000;
    }
    /* End text Founders */

    /* Begin text Hosts profile */
    &__text8-14 {
        font-size: 14px;
        color: #a9a9a9;
    }
    /* End text Hosts profile */

    /* Begin text Host Dashboard */
    &__text9-15{
        font-size: 15px;
    }
    &__text10-13{
        font-size: 13px;
    }
    &__text11-18-green{
        @extend .hl-item__title10-18;
        color: #4ED08D;
    }
    /* End label Host Dashboard */

    /* Begin title Tourist Dashboard */
    &__title12-15-white {
        font-size: 15px;
        color: #FFFFFF;
    }
    /* End title Tourist Dashboard */
}

@media (max-width: 1199px) {
    .hl-item{
        &__w425{
            width: 100%;
            background-position: 97% center !important;
        }
    }
}